@import '~scss/variables';
@import '~scss/custom-properties';

dw-check-in-widget, dw-check-in-widget.sidebar-item, dw-manual-check-in-widget, dw-manual-check-in-widget.sidebar-item {
  cursor: default;

  &:hover {
    color: $sidebar-item-color;
    background: $sidebar-item-bg-color;
    border-color: $sidebar-item-border-color;
  }

  ff-btn-group {
    position: absolute;
    top: 50%;
    right: 5px;
    transform: translateY(-50%);
  }

  // Check In / Check Out buttons
  ff-btn[sense='checkIn'] button, ff-btn[sense='checkOut'] button {
    max-width: 46px;
    white-space: normal;
    line-height: 8px;
    padding: 4px 5px;
    font-size: 9px;
    font-weight: bold;

    &.btn-warning {
      background: #fff;
    }
  }

  ff-btn.ff-spin::before {
    left: 50% !important;
  }


  @media (min-width: $screen-sm) {
    span.fa.fa-check::before {
      display: none;
    }
  }
}

dw-manual-check-in-widget, dw-manual-check-in-widget.sidebar-item {
  padding: 0;
  border: 0;
  z-index: 0;

  & > .sidebar-item {
    display: flex;
    align-items: center;
    //min-height: 34px;
    padding-top: 3px;
    padding-bottom: 2px;
    padding-right: 52px;

    & > span {
      word-break: break-word;
    }

    &:hover {
      color: $sidebar-item-color;
      background: $sidebar-item-bg-color;
      border-color: $sidebar-item-border-color;
      cursor: auto;
    }
  }

  ff-btn[sense="collapse"] .ff-btn {
    border: 0;
    box-shadow: none;
  }

  ff-btn[sense="plus"] .ff-btn, ff-btn[sense="minus"] .ff-btn {
    height: 100%;
    width: 100%;
    border: 0;
    box-shadow: none;
  }

  .ng-validate-alert {
    width: 162px;
    max-width: 162px;
    text-transform: none;
  }

  .check-in-count {
    justify-content: center;
    border-radius: 50%;
    height: 22px;
    line-height: 22px;
    background-color: var(--brand-color);
    color: #fff;
  }
}
