membership-filter {
  flex-wrap: wrap;
  margin-bottom: 5px;

  display: flex;

  & > .mf-status:last-of-type {
    margin-left: 0;
  }

  .mf-all {
    flex: 0 0 210px;
  }

  .mf-role {
    flex: 0 0 210px;
  }

  @media (min-width: 1080px) {
    .mf-all, .mf-role {
      flex: 0 0 auto;
      margin-right: 15px;
    }
  }


  &[layout="2col"] {
    .mf-all {
      order: 10;
    }
    @media (min-width: 1080px) {
      .mf-all, .mf-role {
        flex: 0 0 50%;
        margin-right: 0;
      }
    }
  }
}
