@import '~scss/variables';

dw-alerts {
  position: fixed;
  width: 240px;
  right: 15px;
  bottom: .5rem;
  z-index: 17010;
}

.dw-alert {
  position: relative;
  padding: 5px;
  margin-bottom: 5px;
  font-size: 12px;
  border: 1px solid transparent;
  border-radius: 4px;

  &.ng-enter, &.ng-leave {
    transition: opacity .5s;
  }
  &.ng-enter, &.ng-leave.ng-leave-active {
    opacity: 0;
  }
  &.ng-leave, &.ng-enter.ng-enter-active {
    opacity: 1;
  }

  &.alert-danger {
    background-color: $color-danger;
    color: #fff;
  }

  &.alert-info {
    background-color: var(--brand-color);
    color: #fff;
  }

  button.close {
    float: right;
    position: relative;
    top: -3px;
    right: 0;
    padding: 0;
    border: 0;
    outline: none;
    background: transparent;
    color: #fff;
    font-size: 21px;
    font-weight: bold;
    line-height: 1;
    text-shadow: 0 1px 0 #fff;
    cursor: pointer;
    opacity: .5;
    appearance: none;

    &:hover, &:focus {
      opacity: 1;
    }
  }
}

.dw-alert-progress {
  display: block;
  position: absolute;
  top: 0;
  right: 2.5%;
  width: 95%;
  height: 1px;
  background-color: #fff;

  &.start {
    width: 0;
  }
}
