.dw-modal {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;

  &.ng-enter {
    transition: opacity .2s ease-out;
    opacity: 0;
  }
  &.ng-enter.ng-enter-active {
    opacity: 1;
  }
  &.ng-leave {
    transition: opacity .2s ease-out;
    opacity: 1;
  }
  &.ng-leave.ng-leave-active {
    opacity: 0;
  }
}

.dw-modal-fade {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255,255,255,0.9);
}

.dw-modal-dialog {
  display: flex;
  position: relative;
  max-width: 640px;
  max-height: 94%;
  margin: 10px;
  padding: 25px;
  border: 1px solid #bbb;
  border-radius: 5px;
  background: #fff;
  z-index: 1;
}

.dw-modal-scroll {
  overflow-y: scroll;
}

