.dw-modal-cross {
  position: absolute;
  top: 0;
  right: 0;
  padding: 9px;
  border: 0;
  outline: 0;
  box-shadow: none;
  font-size: 30px;
  line-height: 17px;
  color: #000;
  background: transparent;
  cursor: pointer;
  opacity: 0.2;
  transition: opacity .15s;

  &::after {
    content: "\d7";
  }

  &:hover {
    opacity: 1;
  }
}

.dw-modal-title {
  font-size: 15px;
  color: var(--brand-color);
  text-transform: uppercase;
}
