dw-select-user {
  display: block;
  position: relative;

  .ff-control {
    flex-direction: column;
    align-items: flex-start;
    line-height: 1;
  }

  .company {
    margin-bottom: 2px;
    font-size: 0.85em;
    font-weight: bold;
  }

  .ava {
    .company, .member {
      margin-right: 30px;
    }
  }

  .ff-static { // .avatar container
    position: relative;
  }

  .avatar {
    position: absolute;
    top: 50%;
    right: 2px;
    width: auto;
    height: 100%;
    max-width: 32px;
    max-height: 32px;
    overflow: hidden;
    z-index: 1;
    transition: 0.2s all ease;
    transform: translateY(-50%);
    border-radius: 50%;
    pointer-events: none;
  }

  &:hover {
    .avatar {
      transform: translateY(-50%) scale(1.75) translateX(-10%);
      border-radius: 0;
    }
  }
}
